import { Block, View, Image, Text, Button } from '@tarojs/components';
import React, { useEffect, useState } from 'react';
import Taro, { getCurrentInstance } from '@tarojs/taro';
import Loading from '@/components/loading/loading';
import NavBar from '@/components/navbar/navbar';
import Empty from '@/components/empty/empty';
import style from './center-credit.module.scss';

const CenterCredit = () => {
  const $instance = getCurrentInstance();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(false);
  }, []);

  const onNavigate = (key: string) => {
    const map = {
      record: '/pages/personal/my-record-score/my-record-score', // 学分记录
      apply: '/pages/personal/withdrawal/withdrawal', // 提现
    };
    Taro.navigateTo({
      url: map[key],
    });
  };
  return loading ? (
    <Loading />
  ) : (
    <>
      <NavBar title="学分中心" setBgImage="https://static-1253442168.file.myqcloud.com/xupload/20210721_bg1.png" noBorder white></NavBar>
      <View className={style.header_bg_wrapper}>
        <View className={style.flex}>
          <View className={`${style.flex} ${style.left_cell}`}>
            <Image
              className={style.avatar}
              src={
                $instance.router?.params.avatar ||
                Taro.getStorageSync('account').avatar_url ||
                'https://img.lycheer.net/avatar/4cba76bb40e4646f75e715310023cff4.png/avatar'
              }></Image>
            <View className={style.content}>
              <View className={`${style.layout}`}>
                <Text className={style.strong}>{$instance.router?.params.credits_num ?? ''}</Text>
                <Text className={style.title}>学分</Text>
              </View>
              <View className={style.subtitle}>
                发表笔记能获得学分哦~
                {/* 8月11日前的学分已自动兑换成余额，请到【
                <Text onClick={() => onNavigate('apply')}>余额提现</Text>】查看 */}
              </View>
            </View>
          </View>
          <View className={style.button} onClick={() => onNavigate('record')} hoverClass="hover-class">
            学分记录
          </View>
        </View>
      </View>
      <View className={style.empty_container}>
        <Empty img="https://static-1253442168.file.myqcloud.com/xupload/20210728_empty.png" desc="还在开发中，免费兑换的商品马上就到哦～" />
      </View>
    </>
  );
};

export default CenterCredit;
